<template>
  <d2-container>
    <template slot="header">
      <el-select
        v-model="serviceName"
        placeholder="请选择服务"
        size="small"
        @change="changeService"
        filterable
        clearable
      >
        <el-option
          v-for="item in serviceOptions"
          :key="item"
          :label="item"
          :value="item"
        >
        </el-option>
      </el-select>
      
      <el-select
        v-model="instanceValue"
        placeholder="请选择您的实例"
        size="small"
        @change="changeInstance"
        filterable
        clearable
        style="width: 450px"
      >
        <el-option
          v-for="(item, index) in instanceOptions"
          :key="index"
          :label="item.instanceId"
          :value="item.instanceId"
        >
        </el-option>
      </el-select>
      <!-- <el-button
        class="filter-item"
        :disabled="!instanceValue"
        type="primary"
        icon="search"
        @click="getAlarmConfig"
        size="small"
        >获取告警信息</el-button
      > -->
    </template>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>过载告警</span>
        <div style="float: right; padding: 3px 0">
          <el-button
            size="mini"
            type="primary"
            plain
            @click="setClick"
            >告警配置</el-button
          >
        </div>
        <div style="float: right; padding: 3px 6px">
          <el-button
            size="mini"
            type="danger"
            plain
            @click="deleteClick"
            >告警清除</el-button
          >
        </div>
      </div>
      <div
        id="myAlarmChart"
        style="width: 80%; height: 400px"
        v-loading="loading"
        element-loading-text="正在向服务器请求数据"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
      ></div>
    </el-card>
    <br />
    <!-- <el-card class="box-card">
        <div slot="header" class="clearfix">
            <span>断链告警</span>
        </div>
        <p>n3_link_broken:{{linkbroken.n3_link_broken}}</p>
        <p>n4_link_broken:{{linkbroken.n4_link_broken}}</p>
        <p>n6_link_broken:{{linkbroken.n6_link_broken}}</p>
        <p>n9_link_broken:{{linkbroken.n9_link_broken}}</p>
    </el-card> -->

    <el-dialog
      title="告警配置"
      :visible.sync="dialogVisible"
      width="600px"
      :before-close="handleClose"
    >
      <el-form ref="form" :model="form" label-width="110px">
        <el-form-item label="告警开关">
          <el-switch
            v-model="form.alarm_switch"
            active-color="#13ce66"
            inactive-color="#ff4949"
          ></el-switch>
        </el-form-item>
        <el-form-item label="CPU告警值">
          <el-input v-model="form.alarm_hd_threshold"></el-input>
        </el-form-item>
        <el-form-item label="内存告警值">
          <el-input v-model="form.alarm_cpu_threshold"></el-input>
        </el-form-item>
        <el-form-item label="硬盘告警值">
          <el-input v-model="form.alarm_mem_threshold"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitConfig">确 定</el-button>
      </div>
    </el-dialog>
  </d2-container>
</template>

<script>
import {
  fetchGetServiceList,
  fetchGetAlarmConfig,
  fetchGetInstanceList,
  fetchSetAlarmConfig,
  fetchGetAlarmOverLoad,
  fetchDelAlarmOvcerLoad,
} from "@/api/demo/index";
export default {
  name: "alarm",
  data() {
    return {
      myChart: null,
      serviceName: "",
      serviceOptions: [],
      instanceOptions: [], //upf下面的实例
      instanceValue: "", //当前选中的实例
      timer: null,
      isFirst: true,
      timerInterval: 10000,
      dialogVisible: false,
      form: {
        alarm_switch: false,
        alarm_mem_threshold: null,
        alarm_cpu_threshold: null,
        alarm_hd_threshold: null,
      },
      overload: {
        cpu_load: 0,
        mem_load: 0,
        hd_load: 0,
      },
      linkbroken: {},
      chartOption: [],
      loading: false,
    };
  },
  mounted() {
    this.getService();
    this.drawLine();
  },
  methods: {
    // 获取服务列表
    getService() {
      fetchGetServiceList({
        pageNo: 1,
        pageSize: 50,
      })
        .then((response) => {
          console.log(response);
          this.serviceOptions = response.doms.filter(
            (item) => !item.startsWith("ace")
          );
          if(this.serviceOptions.length > 0) {
            this.serviceName = this.serviceOptions[0]
            this.getServiceInstance(this.serviceName)
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 选择服务
    changeService(val) {
      if (!val) {
        return;
      }
      this.getServiceInstance(val);
    },
    // 选择实例
    changeInstance(val) {
      // 图表置空
      var option = this.myChart.getOption();
      option.series = []
      option.xAxis =[]
      this.myChart.setOption(option);

      this.getOverload()
      //清除定时并重启
      if(this.timer) {
        clearInterval(this.timer);
        this.timer = null;
        this.setTimer();
      }else {
        this.setTimer();
      }
    },
    //获取服务下面的实例
    getServiceInstance(serviceName) {
      fetchGetInstanceList({ serviceName: serviceName })
        .then((res) => {
          this.instanceOptions = res.hosts;
          if(this.isFirst === true && this.instanceOptions.length > 0) {
            this.instanceValue = this.instanceOptions[0].instanceId
            this.changeInstance(this.instanceValue)
          }
          this.isFirst = false
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    setTimer() {
      if (this.timer == null) {
        this.timer = setInterval(() => {
          console.log("开始定时...每过一-执行一次");
          this.getOverload();
        }, this.timerInterval);
      }
    },
    setClick() {
      this.getAlarmConfig()
      this.dialogVisible = true;
    },
    // 获取过载告警数据
    getOverload() {
      this.loading = true;
      fetchGetAlarmOverLoad({
        request_type: "alarm get",
        instance_id: this.instanceValue,
        sequence_no: 3001,
        data: this.form,
      })
        .then((response) => {
          this.loading = false;
          this.overload.cpu_load = response.cpu_load;
          this.overload.mem_load = response.mem_load;
          this.overload.hd_load = response.hd_load;

          //刷新图表警告数据
          var option = this.myChart.getOption();
          console.log(option);
          option.series[0].data.push(response.cpu_load)
          option.series[1].data.push(response.mem_load)
          option.series[2].data.push(response.hd_load)
          // 横坐标更新
          // option.xAxis.data.shift()
          // 现在的时间
          var myDate = new Date()
          console.log('9999');
          console.log(option.xAxis);
          option.xAxis[0].data.push(myDate.getHours()+':'+myDate.getMinutes()+':'+myDate.getSeconds());
          this.myChart.setOption(option);
        })
        .catch((e) => {
          this.loading = false;
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    getAlarmConfig() {
      fetchGetAlarmConfig({
        request_type: "alarm get",
        instance_id: this.instanceValue,
        sequence_no: 3001,
      })
        .then((response) => {
          this.form = response;
          // 纵坐标数据
          // option.series[0].data = [
          //   response.alarm_cpu_threshold,
          //   response.alarm_mem_threshold,
          //   response.alarm_hd_threshold,
          // ];

          // clearInterval(this.timer);
          // this.timer = null;
          // this.setTimer();
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    drawLine() {
      // 初始化echarts实例
      this.myChart = this.$echarts.init(
        document.getElementById("myAlarmChart")
      );
      this.myChart.setOption({
        title: {
          text: "负载值图表",
          subtext: "单位：%",
        },
        tooltip: {
          trigger: "axis",
        },
        legend: {
          data: ["CPU", "内存", "硬盘"],
        },
        // xAxis: {
        //     data: [1,2,3],
        //     type: 'value',
        //     scale: true,
        //     show: false,
        //     minInterval: 1,
        //     max: 30,
        //     min: 0,
        //     splitLine: {
        //       show: false
        //     }
        // },
        xAxis: {
            type: 'category',
            data: ["Begin"]
        },
        yAxis: [
          {
            type: "value",
          },
        ],
        series: [
          {
            name: "CPU",
            type: "line",
            data: [10],
            // data: [this.form.alarm_cpu_threshold, this.form.alarm_mem_threshold, this.form.alarm_hd_threshold],
          },
          {
            name: "内存",
            type: "line",
            data: [8],
            // data: [this.overload.cpu_load, this.overload.mem_load, this.overload.hd_load],
          },
          {
            name: "硬盘",
            type: "line",
            data: [6],
            // data: [this.overload.cpu_load, this.overload.mem_load, this.overload.hd_load],
          },
        ],
      });
    },
    handleClose() {
      this.dialogVisible = false;
    },
    submitConfig() {
      this.dialogVisible = false;
      fetchSetAlarmConfig({
        request_type: "alarm set",
        instance_id: this.instanceValue,
        sequence_no: 3001,
        data: this.form,
      })
        .then((response) => {})
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 告警清除
    deleteClick() {
      this.$confirm('是否清除数据？', '确认信息', {
        distinguishCancelAndClose: true,
        confirmButtonText: '确认',
        cancelButtonText: '取消'
      })
        .then(() => {
          this.loading = true;
          fetchDelAlarmOvcerLoad({
            request_type: "alarm del",
            instance_id: this.instanceValue,
            sequence_no: 4004,
          })
            .then((response) => {
              this.loading = false;
              this.$message({
                type: 'su',
                message: '操作成功'
              });
            })
            .catch((e) => {
              this.loading = false;
              console.log(e);
              this.$alert(e, "网络请求发生错误", {
                confirmButtonText: "确定",
                callback: (action) => {},
              });
            });
        })
        .catch(action => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        });
    }
  },
  destroyed: function () {
    // 每次离开当前界面时，清除定时器
    clearInterval(this.timer);
    this.timer = null;
  },
};
</script>
