import fetch from '@/plugin/axios'
import fetch1 from '@/plugin/axios/deviceAxios'
import fetch2 from '@/plugin/axios/setAxios'

/**
 * 设备管理 服务实例列表请求
 * @param {*} query
 */
export function fetchGetInstanceList(query) {
  return fetch1({
    url: '/nacos/v1/ns/instance/list',
    method: 'get',
    params: query
  })
}
/**
 * 设备管理 获取服务列表请求
 * @param {*} query
 */
export function fetchGetServiceList(query) {
  return fetch1({
    url: '/nacos/v1/ns/service/list',
    method: 'get',
    params: query
  })
}
/**
 * 设备管理 删除服务实例
 * @param {*} query
 */
export function fetchDeleteInstance(query) {
  return fetch1({
    url: '/nacos/v1/ns/instance',
    method: 'delete',
    params: query
  })
}
/**
 * 设备管理 添加服务实例
 * @param {*} query
 */
export function fetchAddInstance(parmas) {
  return fetch1({
    url: '/nacos/v1/ns/instance',
    method: 'post',
    data: parmas
  })
}
/**
 * 设备管理 编辑服务实例
 * @param {*} query
 */
export function fetchUpdateInstance(parmas) {
  return fetch1({
    url: '/nacos/v1/ns/instance',
    method: 'put',
    data: parmas
  })
}

//============================= 配置管理 ==================================

/**
 * 配置管理DPE Configuration和CPE Configuration 请求
 * @param {*} query
 */
export function fetchGetConfig(query) {
  return fetch2({
    url: '/api/upf/nfom/v1.0/config/get',
    method: 'post',
    data: query,
    headers: { 'Content-Type': 'application/json;charset=UTF-8' }
  })
}
/**
 * 配置管理DPE Configuration和CPE Configuration设置
 * @param {*} query
 */
export function fetchSetConfig(query) {
  return fetch2({
    url: '/api/upf/nfom/v1.0/config/set',
    method: 'post',
    data: query
  })
}
